import styles from './Users.module.css';
import Paginator from '../common/Paginator/Paginator';
import User from './User';

let Users = ({ currentPage, onPageChanged, totalUsersCount, pageSize, users, unfollow, follow, followingInProgress, ...props }) => {
    return <div>
        <Paginator currentPage={currentPage} onPageChanged={onPageChanged} totalItemsCount={totalUsersCount} pageSize={pageSize} />
        <h2>Users</h2>
        <div className={styles.userList}>
            {users.map(user => <User user={user} followingInProgress={followingInProgress} unfollow={unfollow} follow={follow} key={user.id} />)}
        </div>
    </div>
}

export default Users;